* {
    box-sizing: border-box;
}

::selection {
    background-color: $clr1;
    color: $clr6;
}

html {
    font-family: montserrat;
    line-height: 1.6;
}

body {
    font-size: rem(18px);
    margin: 0;
    margin: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: Circular;
    margin: 0 0 0.6em;
    line-height: 1;
}

section,
main,
footer {
    padding: 0px;
}

img {
    width: 100%;
}

p {
    margin: 0 0 1em;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}